@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@400;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@400;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar{
    min-height: 55px;
    width: 100%;
    background-color: lightslategray;
}

.title{
    font-size: 24pt;
    font-family: 'Cinzel', serif;
    float: left;
    width: 30%;
    font-weight: 700;
    padding: 5px;
    text-shadow: 2px 2px 5px lightgrey;
}

.links{
    top: 50%;
    float: right;
    width: 65%;
    margin-top: 10px;
}

.linkText{
    font-family: 'Cinzel', serif;
    float: left;
    padding: 5px;
    margin-left: 5px;
    margin-right: 5px;
    text-decoration: none;
    font-size: 14pt;
    color: black;
    font-weight: 700;
    background-color: lightgray;
    border-radius: 0.5em;
    box-shadow: -1px 2px 3px 0 black;
}
.linkText:visited{
    color: black;
}

.clearboth{
    clear: both;
}
.usernav{
    float: right;
    margin-right: 25px;
    overflow: auto;
}
.field{
    margin: auto;
    width: 80%;
    margin-bottom: 10px;
}

.label{
    display: inline-block;
    width: 130px;
    margin-right: 15px;
}
.sectionHeader{
    padding-top: 25px;
    margin-bottom: 25px;
    font-family: 'Cinzel', serif;
    font-weight: 700;
    font-size: 20pt;
}
.formError{
    color: red;
    margin: 0px;
}
Body{
    background-color: lightgoldenrodyellow;
}
.signupForm{
    width: 40%;
    padding: 15px;
    margin: auto;
    background-color: lightgrey;
    box-shadow: 0 4px 8px 0 lightslategray;
}
.statL{
    margin: auto;
    width: 50%;
    float: left;
}
.statR{
    margin: auto;
    width: 50%;
    margin-bottom: 10px;
    float: right; 
}
.statW{
    margin: auto;
    width: 50%;
    float: left;
}
.clearBoth{
    clear: both;
}
.fieldWrapper{
    width: 80%;
    margin: auto;
    margin-bottom: 10px;
}
.fieldWrapperW{
    width: 90%;
    margin: auto;
    margin-bottom: 10px;
}
.interiorLinkText{
    font-family: 'Cinzel', serif;
    padding: 10px;
    text-decoration: none;
    font-size: 14pt;
    color: black;
    font-weight: 700;
    background-color: lightgray;
    margin-right: 10px;
    border-radius: .5em;
    box-shadow: 0 2px 6px 0 lightslategray;
}
.infoLine{
    font-family: 'Cinzel', serif;
    font-weight: 700;
    float: left;
    margin: auto;
    padding-right: 10px;
}
.charSheet{
    width: 50%;
    margin: auto;
    margin-bottom: 20px;
    padding: 5px;
    background-color: lightgray;
    border-radius: 0.25em;
    box-shadow: 0 4px 8px 0 lightslategray;
}
.charForm{
    padding: 20px;
    width:50%;
    margin: auto;
    background-color: lightgray;
    border-radius: 0.5em;
    box-shadow: 0 4px 8px 0 lightslategray;
}
.edit{
    width: 75px;
    background-color: lightsteelblue;
    font-family: 'Cinzel', serif;
    font-weight: 700;
    border-radius: 0.5em;
    box-shadow: 0 2px 6px 0 lightslategray;
    margin: 5px;
    float: left;
}
.delete{
    width: 75px;
    background-color: lightcoral;
    font-family: 'Cinzel', serif;
    font-weight: 700;
    border-radius: 0.5em;
    box-shadow: 0 2px 6px 0 lightslategray;
    margin: 5px;
    float: left;
}
